import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import courses from "../views/courses.vue";
import news from "../views/news.vue";
import contact from "../views/contact.vue";
import about from "../views/about.vue";
import instructior from "../views/instructior.vue";
import pricing from "../views/pricing.vue";
import faq from "../views/faq.vue";
import crs from "../views/crs-details.vue";
import blg from "../views/blg-details.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/courses",
    name: "courses",
    component: courses
  },
  {
    path: "/news",
    name: "news",
    component: news
  },
  {
    path: "/contact",
    name: "contact",
    component: contact
  },
  {
    path: "/about",
    name: "about",
    component: about
  },
  {
    path: "/instructior",
    name: "instructior",
    component: instructior
  },
  {
    path: "/pricing",
    name: "pricing",
    component: pricing
  },
  {
    path: "/faq",
    name: "faq",
    component: faq
  },
  {
    path: "/course-details",
    name: "course-details",
    component: crs
  },
  {
    path: "/blog-details",
    name: "blog-details",
    component: blg
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
